export default {
	data() {
		return {
			title: '',
			content: '',
			contact: '',
			show: false,
			num: 3
		};
	},
	onLoad() {







































	},
	methods: {
		adShow(InterstitialAd) {















		},
		commit() {
			if (this.content.length === 0 || this.title.length ===0 ) {
				uni.showToast({
					title: '请输入标题和内容',
					icon: 'none'
				})
				return
			}
			const count = this._getByteLen(this.content)
			const count2 = this._getByteLen(this.title)
			if (count > 100 || count2 > 30) {
				uni.showModal({
				    title: '提示',
				    content: `标题最多30字，内容最多200字\n当前标题${count2}字，内容${count}字`,
					showCancel: false,
				    success: (res) => {
				        if (res.confirm) {
							
				        }
				    }
				})
			} else {
				// commit
				this.doRequest('/feedback', {
					uuid: this.$store.state.uuid,
					title: this.title,
					content: this.content,
					contact: this.contact
				}).then(res=>{
					this.backAction()
				}).catch(_=>{
					this.backAction()
				})
			}
		},
		_getByteLen(v) {
		    var len = 0;
		    for (var i = 0; i < v.length; i++) {
		        var a = v.charAt(i);
		        if (a.match(/[^\x00-\xff]/ig) != null)
		        {
		            len += 2;
		        }
		        else
		        {
		            len += 1;
		        }
		    }
		    return Math.ceil(len/2);
		},
		backAction() {
			this.show = true
			if(this.num <= 0) {
				uni.switchTab({
				    url: '/pages/tabbar/tabbar-4/tabbar-4'
				});
				return
			}
			this.num--
			setTimeout(()=>{
				this.backAction()
			},1000)
		}
	},
	onShareAppMessage(res) {
		uni.showToast({
			title: 'share'
		})
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid
	    }
	}
};
